import * as Yup from 'yup';

export const validationSchema = Yup.object({
  name: Yup.string()
    .required('\'  \' '),
  email: Yup.string()
    .email('   ')
    .required('   \''),
  phone: Yup.string()
    .matches(/^\+?[0-9]+$/, '   ')
    .min(10, ' 10 ')
    .max(15, ' 15 ')
    .required('   \' '),
});